import React, { Component } from 'react';
import limexperience from '../img/limexperience_pic.jpg'
class MyCardImage extends Component {
    render() {
        return (
            <div>
                
                <div className="carousel-inner">
                    <div className="carousel-item active">
                    <img src={limexperience} className="d-block w-100 gradient-effect" alt="..."/>
                    <div className="carousel-caption d-none d-md-block">
                        <h1 className="mysubtitle">The best experience for you</h1>
                        <p className="mysubtitle c-grey">We promise you, to have the best experience, our customers are kings!</p>
                    </div>
                    </div>
                </div>
            </div>
        );
    }
}

export default MyCardImage;