import React, { Component } from 'react';
import Container from 'react-bootstrap/Container'
class MyFooter extends Component {
    render() {
        const bgcolorfooter={backgroundColor:'black',color:'white'}
        return (
            <div>
                <div className=" pt-5 pb-5"  fluid="true" style={bgcolorfooter}>
                    <Container>
                    <div className="row">
                        <div className="col-md-12">
                            <h6 className="text-center "> All rights reserved &copy; Developed by <a style={bgcolorfooter} href="http:\\www.devbinary.c1.biz" target="_blank" rel="noopener noreferrer"> Devbinary </a>up to {new Date().toDateString()}</h6>
                        </div>
                    </div>
                     </Container>
                </div>
                
            </div>
        );
    }
}

export default MyFooter;