import React, { Component } from 'react';
import Navbar from 'react-bootstrap/Navbar'
import Nav from 'react-bootstrap/Nav'
import { Link } from 'react-router-dom';
class MyNavbar extends Component {
    render() {
        return (
            <div>
                <Navbar className="mytransparent p-4" collapseOnSelect expand="lg" bg="dark" variant="dark">
                    <Navbar.Brand href="/limcompany/">Limousine LOGO</Navbar.Brand>
                    <Navbar.Toggle aria-controls="responsive-navbar-nav" />
                    <Navbar.Collapse id="responsive-navbar-nav">
                        <Nav className="mr-auto px-3 mx-3">
                        <Nav.Link className="mylink mx-3 px-3" href="/limcompany/">Home</Nav.Link>
                        <Nav.Link className="mylink mx-3 px-3" href="#fleet">Fleet</Nav.Link>
                        <li className="nav-item mx-3 px-3">
                                <Link className="nav-link no-underline mylink" to="/Booknow">Book now</Link>
                        </li> 
                        <Nav.Link className="mylink mx-3 px-3" href="#services">Services</Nav.Link>
                        <Nav.Link className="mylink mx-3 px-3" href="#pricing">Contact</Nav.Link>
                        <Nav.Link className="mylink mx-3 px-3" href="#pricing">Blog</Nav.Link>
                        </Nav>
                        <Nav>
                        <Nav.Link href="#deets">More deets</Nav.Link>
                        <Nav.Link eventKey={2} href="#memes">
                            Dank memes
                        </Nav.Link>
                        </Nav>
                    </Navbar.Collapse>
                </Navbar>
            </div>
        );
    }
}

export default MyNavbar;