import React, { Component } from 'react';
import rangerover from '../img/Limo-Range-Rover.jpg'
import limford from '../img/limousineford.jpg'
import limsedan from '../img/limousinesedan.jpg'
import limroyce from '../img/limousineroyce.jpg'
import hammerlim from '../img/hammerlim.jpg'
import lincolnlim from '../img/lincolnlim.jpeg'

class Fleet extends Component {
    render() {
        return (
            <div id="fleet" className="p-3 m-3">
                
                <div className="row">
                    <div className="col-md-1"></div>
                    <div className="col-md-10">
                        <div className="row">
                            <div className="col-md">
                            <h2 className="mysubtitle text-center pb-4">Fleet</h2>
                            </div>
                        
                        </div>
                        <div className="row">
                        <div className="card-deck">
                            <div className="card img-effect">
                                <h5 className="card-header text-center mysubtitle">Range Rover Lim</h5>
                                <img src={rangerover} className="card-img-top" alt="..."/>
                                <div className="card-img-overlay">
                                </div>
                            </div>
                         
                            
                            <div className="card img-effect">
                            <h5 className="card-header text-center mysubtitle">Lincoln Lim</h5>
                                <img src={lincolnlim} className="card-img-top" alt="..."/>
                                <div className="card-img-overlay">
                                </div>
                          
                            </div>
                          
                            <div className="card img-effect">
                            <h5 className="card-header text-center mysubtitle">Hammer Lim</h5>
                                <img src={hammerlim} className="card-img-top pt-5" alt="..."/>
                                <div className="card-img-overlay">
                                </div>
                            </div>
                        </div>
                            
                        </div>
                        <div className="row pt-3 mt-4">
                        <div className="card-deck">
                            <div className="card img-effect">
                                <h5 className="card-header text-center mysubtitle">Sedan</h5>
                                <img src={limsedan} className="card-img-top" alt="..."/>
                                <div className="card-img-overlay">
                                </div>
                            </div>
                         
                            <div className="card img-effect">
                            <h5 className="card-header text-center mysubtitle">Rolls Royce</h5>
                                <img src={limroyce} className="card-img-top" alt="..."/>
                                <div className="card-img-overlay">
                                </div>
                            </div>
                            
                            <div className="card img-effect">
                            <h5 className="card-header text-center mysubtitle">Ford</h5>
                                <img src={limford} className="card-img-top" alt="..."/>
                                <div className="card-img-overlay">
                                </div>
                          
                            </div>
                          
                        </div>
                            
                        </div>
                    </div>
                    <div className="col-md-1"></div>
                </div>

            </div>

        );
    }
}

export default Fleet;