import React, { Component } from 'react';
import newyork_pic from '../img/newyork_wallpaper.jpg'
import Image from 'react-bootstrap/Image'

class Body1 extends Component {
    render() {
        const interiorlim_pic={backgroundColor:'white'}
        return (
            <div>
                <div className="jumbotron pb-4 mb-4" style={interiorlim_pic}>
                    <h2 className="text-center display-3 pb-3 mb-3">We bring luxe for your..</h2>
                <Image className="" src={newyork_pic} fluid />
                </div>
            </div>
        );
    }
}

export default Body1;