import React, { Component } from 'react';
import Layout from '../Layout/Layout';
import { Button } from 'react-bootstrap';

class BookNow extends Component {
    render() {
        return (
            <div>
                <Layout>
                    <h3 className="mt-4 pt-4 p-4 m-4 text-center display-3">Book now</h3>

                    <div className="row">
                        <div className="col-md-2"></div>
                        <div className="col-md-8">
                            <form className="border p-5 bg-light">
                            <div className="form-group">
                                <label>Select service type</label>
                                <select className="custom-select">
                                    <option value>Open this select menu</option>
                                    <option value="1">Airport Pickup</option>
                                    <option value="2">Transfert P2P</option>
                                    <option value="3">Airport Dropoff</option>
                                    <option value="4">Hourly/As Directed</option>
                                    </select>
                            </div>
                            <div className="form-group">
                                <div className="row">
                                    <div className="col-md-8">
                                    <label>Pick-Up date</label>
                                    <input className="form-control" type="date" ></input>    
                                    </div>
                                    <div className="col-md-4">
                                    <label>Pick-Up time</label>
                                    <input className="form-control" type="time" ></input>   
                                    </div>
                                </div>                            
                            </div>
                            <div className="form-group">
                                <label>Pick-Up location</label>
                                <input className="form-control" type="text" ></input>  
                            </div>
                            <div className="form-group">
                                <label>Drop-Off location</label>
                                <input className="form-control" type="text" ></input>  
                            </div>
                            <Button className="btn-info btn-block">Select vehicule</Button>
                            </form>
                        </div>
                        <div className="col-md-2"></div>
                    </div>
                </Layout>
            </div>
        );
    }
}

export default BookNow;