import React, { Component } from 'react';

class MySubNav extends Component {
    render() {
        return (
            <div>
                <div className="p-2 bg-light text-dark">                
                    <div className="row">
                
                        <div className="col-md">
                            <div className="media border border-dark mt-1">
                
                                    <div className="myicon px-2 mx-2 text-dark text-center mx-auto">
                                    <h6 className="fa fa-phone " aria-hidden="true"></h6>
                                        
                                    <h6 className="mt-1 pt-2 text-center float-right mx-2 px-3">(+1) 164-113-213</h6>
                                    </div>
                                    
                            </div>
            
                        </div>

                        <div className="col-md">
                            <div className="media border border-dark mt-1">
                
                                <div className="myicon px-2 mx-2 text-dark text-center mx-auto">
                                <h6 className="fa fa-envelope" aria-hidden="true"></h6>
                                    
                                <h6 className="mt-1 pt-2 text-center float-right mx-2 px-3">support@limcompany.com</h6>
                                </div>
                                
                            </div>
                        </div>
              
                    </div>
                </div>
            </div>
        );
    }
}

export default MySubNav;